.userList {
  background-color: #1A1A1A;
  display: flex;
  justify-content: center;
  align-items: center;

  .userListTitle {
    padding: 20px 0px;
  }

  .itemUser {
    background-color: #2D2D2D;
    font-size: 20px;
    text-align: start;
    padding: 10px 20px;
    margin-bottom: 20px;
    border-radius: 8px;

    &:hover {
      color: deeppink;
    }
  }
}